import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import StarDataListComponent from './StarDataListComponent';
import { Switch, Route } from 'react-router-dom';
import StarDataComponent from './StarDataComponent';
import StarDetailsPage from './star-details-page';
import Home from './Home';

class App extends Component {
  render() {
    return (
      <div className="App">
        <Switch>
          <Route path="/" component={Home}></Route>
          <Route path="/list" component={StarDataListComponent}></Route>
          <Route path='/star/:id' component={StarDataComponent}></Route>
          <Route path='/star-details-page/:id' component={StarDetailsPage}></Route>
        </Switch>
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h1 className="App-title">Welcome to React</h1>
        </header>
        <p className="App-intro">
          To get started, edit <code>src/App.js</code> and save to reload.
        </p>
        {/* <StarDataListComponent /> */}
      </div>
    );
  }
}

export default App;



